<template>
  <div>
    <van-tabbar  v-model="active" >
      <van-tabbar-item v-for="(foot,i) in foots" :to="foot.name" :info="i==2?carNum:''" :key="i" :name="foot.name" :icon="foot.icon">{{foot.txt}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {mapState} from "vuex"
export default {
  data() {
    return {
      foots: [
        { txt: "首页", path: "/index/home", name: "home", icon: "wap-home" },
        {
          txt: "分类",
          path: "/index/classify",
          name: "classify",
          icon: "qr-invalid"
        },
        {
          txt: "购物车",
          path: "/index/cart",
          name: "cart",
          icon: "shopping-cart-o"
        },
        { txt: "我", path: "/index/mine", name:"mine",icon: "friends-o" }
      ]
    };
  },
  computed:{
    ...mapState([
      "active",
      "carNum",
    ])
  },
 mounted(){
    // if(sessionStorage.userInfo){
    //   this.$store.commit("changeIsLogin",isLogin)
    // }
    let usernames = JSON.parse(sessionStorage.userInfo).username;
    this.$axios.post("/vue/getShopcarInfo",{username:usernames}).then(res=>{
        let total = res.data.total
        this.$store.commit("changeCarNum",total)
    })
    
  },
};
</script>

