import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		active: 'name',
		isLogin: true,
		detail: {},
		value: '1',
		AddressInfo: {},
		argu: '选择参数',
		carNum:0,
		orderList:[],
		nowAds:{},
	},
	mutations: {
		changeActive(state, active) {
			state.active = active;
		},
		changeIsLogin(state, isLogin) {
			state.isLogin = isLogin;
		},
		changeAddressInfo(state, msg) {
			state.AddressInfo.name = msg.addressName;
			state.AddressInfo.tel = msg.addressTel;
			state.AddressInfo.province = msg.province;
			state.AddressInfo.city = msg.city;
			state.AddressInfo.county = msg.county;
			state.AddressInfo.addressDetail = msg.addressDetail;
			state.AddressInfo.postalCode = msg.postCode;
			state.AddressInfo.isDefault = msg.isDefault;
			state.AddressInfo.areaCode = msg.areaCode;
		},
		changeCarNum(state,num){
			state.carNum = num;
		},
		changeOrderList(state,num){
			state.orderList=[]; 
			num.forEach(element => {
				state.orderList.push(element)
			});
		},
		chanegNowAds(state,ads){
			state.nowAds={};
			for(let item in ads){
				state.nowAds[item] = ads[item]
			}
		}
	},
	
	actions: {}
});
