import Vue from 'vue';
import Router from 'vue-router';
import Index from './views/Index.vue';

Vue.use(Router);

export default new Router({
	routes: [
    {
      path:"/",
      redirect:{name:"home"}
    },
		{
			path: '/',
			name: 'index',
			component: Index,
			children: [
				{ path: 'home', name: 'home', component: () => import('@/views/Home.vue') },
				{ path: 'classify', name: 'classify', component: () => import('@/views/Classify.vue') },
				{ path: 'cart', name: 'cart', component: () => import('@/views/Cart.vue') },
				{ path: 'mine', name: 'mine', component: () => import('@/views/Mine.vue') }
			]
		},
		{
			path: '/register',
			name: 'register',
			component: () => import('@/views/Register.vue')
		},
		{
			path: '/login',
			name: 'login',
			component: () => import('@/views/Login.vue')
    },
    {
      path:'/search',
      name:'search',
      component:()=>import("@/views/Search.vue")
	},
	{
		path:'/goodDetail/:goodId?',
		name:'goodDetail',
		component:()=>import("@/views/GoodDetail.vue")
	},
	{
		path:"/changeads/:adsNow?",
		name:"changeads",
		component:()=>import("@/views/ChangeAds.vue")
	},
	{
		path:"/chooseads",
		name:"chooseads",
		component:()=>import("@/views/ChooseAds.vue")
	},
	{
		path:"/suborder/:ids?",
		name:"suborder",
		component:()=>import("@/views/SubOrder.vue")
	},
    {
      path:"**",
      redirect:{name:'home'}
    }
	]
});
